import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

import axios from 'axios';
export default new Vuex.Store({
  state: {
    blogs: [],
    blogPages: [],
  },
  mutations: {
    createBlogPages: (myState, allBlogs) => {
      myState.blogPages = [];
      while (allBlogs.length)
        myState.blogPages.push(allBlogs.splice(0,10));
    },
    getAllBlogs: (myState, allBlogs) => {
      myState.blogs = allBlogs;
    }
  },
  actions: {
    getBlogsIntoPages: context => {
      axios
      .get('https://jsonplaceholder.typicode.com/posts')
      .then((response)=>{
        context.commit('createBlogPages', response.data);
      });   
    },
    getAllBlogs: context => {
      axios
      .get('https://jsonplaceholder.typicode.com/posts')
      .then((response)=>{
        context.commit('getAllBlogs', response.data);
      });   
    }
  } 
})
