<template>
  <div id="showBlogs">
    <h1>All Blog Articles</h1>
    <nav>
      <ul v-for="n in nPages" :key="n">
        <li><router-link v-bind:to="'/blogPage/'+ n">{{n}}</router-link></li>
      </ul>
    </nav>
    <input type="text" v-model="search" placeholder="search blogs"/>
  <div v-for="blog in filteredBlog" :key="blog.id" class="singleBlog">
    <router-link v-bind:to="'/blog/' + blog.id">
    <h2>{{blog.title|toUpperCase}}</h2>
    </router-link>
    <article>{{blog.body|sneppet}}</article>
  </div>
  </div>
</template>

<script>

export default {
  name: 'showBlogs',
  data() {
    return {
      search: '',
    };
  }, 
  created() {
    this.$store.dispatch('getAllBlogs');
    this.$store.dispatch('getBlogsIntoPages');
  },
  computed: {
    blogs() {
      return this.$store.state.blogs;
    },
    nPages(){
      return this.$store.state.blogPages.length;
    },
    filteredBlog() {
      return this.blogs.filter((blog) => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    sneppet(value) {
      return value.slice(0,100) + '...';
    }
  }
}
</script>

