<template>
  <div id="singleBlog">
    <h1>{{blog.title|toUpperCase}}</h1>
    <article class="singleBlog">{{blog.body}}</article>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'showBlogs',
  data() {
    return {
      blog: {},
      id: this.$route.params.id
    };
  }, 
  created() {
    axios
    .get('https://jsonplaceholder.typicode.com/posts/' + this.id)
    .then((response)=>{
      this.blog = response.data;
    });
  },
}
</script>

